ul.navbar{
list-style-type:none;
background-color:grey;
overflow:hidden;
margin:0;
padding:0;
}

ul.navbar li{
  float:left;
  width:20%;
  
}

ul.navbar li a{
  color:white;
  text-decoration: none;
  display: block;
  text-align:center;
  padding:14px 16px;
}

ul.navbar li a:hover{
  color:white;
  text-decoration: none;
  background-color:black;
}

.page{
  display:flex;
  height:800px;
  justify-content: center;
} 

.container{
  width:80%;
  background-color:#f5f5f5;
}